<script>
    export default {
        render() {},
        data() {
            return {
                list: [],
                paging: {
                    page: 1,
                    size: 10
                },
                loading: true,
                hasMore: true
            }
        },
        computed: {
            userInfo() {
                return this.$store.getters['common/getUserInfo']
            }
        },
        async created() {
            this.paging.page = 1
            await this.$nextTick()
            console.log('------------------------');
            this.getList()
        },
        methods: {
            async getList() {
                // override this method
            },
            scorllEvt(evt) {
                let target = evt.target,
                    fullHeight = target.scrollHeight,
                    viewHeight = target.clientHeight,
                    disHeight = target.scrollTop,
                    isBottom = (fullHeight - viewHeight) <= (disHeight + 12)

                if (isBottom && !this.loading && this.hasMore) {
                    this.paging.page += 1
                    this.getList()
                }
            },
            btnEvt(type, row) {
                switch (type) {
                    case 'edit':
                        this.editEvt(row)
                        break
                    case 'approve':
                        this.approveEvt(row)
                        break
                    case 'delete':
                        this.$dialog.confirm({
                                title: '删除提示',
                                message: `确定要删除这条信息？`,
                            }).then(async () => {
                                this.deleteEvt(row)
                            }).catch(() => {})
                        break
                    case 'query':
                        this.paging.page = 1
                        this.getList()
                        break
                    default:
                        break
                }
            },
            editEvt() {
                // override this method
            },
            approveEvt() {
                // override this method
            },
            deleteEvt() {
                // override this method
            }
        }
    }
</script>