import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

Vue.use(Router)

// 引入组件
// import Login from '../views/login'
// import Home from '../views/home'
// import MaterialList from '../views/material'
// import MaterialEdit from '../views/material/Edit'
// import SupplierList from '../views/supplier'
// import SupplierEdit from '../views/supplier/Edit'
// import ProjectList from '../views/project'
// import ProjectEdit from '../views/project/Edit'
// import PurchaseList from '../views/purchase'
// import PurchaseEdit from '../views/purchase/Edit'
// import PurchaseDetail from '../views/purchase/Detail'
// import ReceiveList from '../views/receiving'
// import ReceiveEdit from '../views/receiving/Edit'
// import ReceiveDetail from '../views/receiving/Detail'
// import Setting from '../views/setting'

// import Empty from '../views/common/Empty'

// 动态引入 dynamic import
const Login = () => {
    // 添加打包别名
    return import(/** webpackChunkName="LoginPage" */'../views/login')
}
const Home = () => import('../views/home')
const MaterialList = () => import('../views/material')
const MaterialEdit = () => import('../views/material/Edit')
const SupplierList = () => import('../views/supplier')
const SupplierEdit = () => import('../views/supplier/Edit')
const ProjectList = () => import('../views/project')
const ProjectEdit = () => import('../views/project/Edit')
const PurchaseList = () => import('../views/purchase')
const PurchaseEdit = () => import('../views/purchase/Edit')
const PurchaseDetail = () => import('../views/purchase/Detail')
const ReceiveList = () => import('../views/receiving')
const ReceiveEdit = () => import('../views/receiving/Edit')
const ReceiveDetail = () => import('../views/receiving/Detail')
const Setting = () => import('../views/setting')
const Version = () => import('../views/setting/Version')
const Operate = () => import('../views/setting/Operate')
const Test = () => import('../views/login/test')

// 实例化Router对象
const router = new Router({
    // mode: "history",
    routes: [
        {path: '', redirect: '/login'},
        {path: '/login', component: Login, meta: {noLogin: true}},
        {path: '/home', component: Home},
        {path: '/material', component: MaterialList},
        {path: '/material/edit', component: MaterialEdit},
        {path: '/supplier', component: SupplierList},
        {path: '/supplier/edit', component: SupplierEdit},
        {path: '/project', component: ProjectList},
        {path: '/project/edit', component: ProjectEdit},
        {path: '/purchase', component: PurchaseList},
        {path: '/purchase/edit', component: PurchaseEdit},
        {path: '/purchase/detail', component: PurchaseDetail},
        {path: '/receiving', component: ReceiveList},
        {path: '/receiving/edit', component: ReceiveEdit},
        {path: '/receiving/detail', component: ReceiveDetail},
        {path: '/setting', component: Setting},
        {path: '/version', component: Version},
        {path: '/operate', component: Operate},
        {path: '/test/:id', component: Test, meta: {noLogin: true}},
    ]
})

router.beforeEach((to, from, next) => {
    // 不需要登录的页面直接去这个页面即可
    if (!!to.meta.noLogin) {
        next()
    } else {
        let token = store.getters['common/getToken']
        // 判断用户是否已经登录
        if (!!token) {
            next()
        } else {
            next('/login')
        }
    }
})

export default router