<template>
    <div class="simple-list">
        <header>
            <input type="text" v-model.lazy="name" placeholder="输入项目名字"/>
            <i @click="btnEvt('query')"></i>
            <i @click="$emit('pickEvent')" class="close"></i>
        </header>
        <ul @scroll="scorllEvt">
            <li v-for="it in list" :key="it.id">
                <span>{{it.name}}</span>
                <em>{{it.id}}</em>
                <i @click="pickUser(it)"></i>
            </li>
            <li v-if="loading" class="more">
                <van-loading color="#1989fa" /> 数据加载中...
            </li>
            <li v-else class="more">
                {{hasMore ? '上滑加载更多数据' : '没有更多数据'}}
            </li>
        </ul>
    </div>
</template>

<script>
    import { purchaseListApi } from '../apis/PurchaseApi'
    import ListHandle from '../views/common/ListHandle.vue'

    export default {
        extends: ListHandle,
        data() {
            return {
                name: ''
            }
        },
        created() {
            this.loading = true
            this.hasMore = true
            this.paging.size = 20
        },
        methods: {
            async getList() {
                let result = await purchaseListApi({
                        projectName: this.name,
                        state: '完成',
                        page: this.paging.page,
                        size: this.paging.size
                    })

                if (result.code === 200 && !!result.data) {
                    this.list = this.paging.page === 1 ? result.data.rows : this.list.concat(result.data.rows)
                    this.total = result.data.total
                    this.hasMore = this.total > (this.paging.page * this.paging.size) 
                }

                this.loading = false
            },
            pickUser(it) {
                this.$emit('pickEvent', it)
            }
        }
    }
</script>