import Ajax from './Ajax'

// JSDoc 注释语法
/**
 * 获取用户登录验证码的接口
 * @param {String} phone 手机号码
 * @returns Promise
 */
const getVdtCodeApi = phone => {
    return Ajax({
        url: '/user/validate/code',
        data: {phone},
        method: 'POST'
    })
}

const userLoginApi = data => {
    return Ajax({
        url: '/user/app/login',
        method: 'POST',
        data
    })
}

const getUserApi = id => {
    return Ajax({
        url: '/user/info',
        params: {id}
    })
}

const getUserRoleApi = ids => Ajax({ url: '/role/list/byids', params: {ids} })

const autoLoginApi = () => {
    return Ajax({
        url: '/user/auto/login',
        method: 'POST'
    })
}

const userListApi = params => {
    return Ajax({
        url: '/user/list',
        params
    })
}

const userSaveApi = data => {
    return Ajax({
        url: '/user/update',
        method: 'POST',
        data
    })
}

export {
    getVdtCodeApi,
    userLoginApi,
    getUserApi,
    getUserRoleApi,
    autoLoginApi,
    userListApi,
    userSaveApi
}