<template>
    <div class="layout-container">
        <header>
            <i @click="btnEvt('back')" v-if="showBack" class="back"></i>
            <i @click="btnEvt('home')" v-else-if="showHome" class="home"></i>
            {{title}}
            <i @click="btnEvt('new')" v-if="showNew" class="new"></i>
        </header>
        <article>
            <slot/>
        </article>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                required: true,
                type: String
            },
            showBack: {
                type: Boolean,
                default: false
            },
            showHome: {
                type: Boolean,
                default: true
            }
        },
        computed: {
            showNew() {
                return !!this.$listeners.addEvent
            }
        },
        mounted() {
            window.setStateBg('#FE5B02')
        },
        methods: {
            btnEvt(type) {
                switch (type) {
                    case 'back':
                        this.$router.go(-1)
                        break
                    case 'new':
                        this.$emit('addEvent')
                        break
                    default:
                        this.$router.push('/home')
                        break
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../style/common.less";
    .layout-container {
        display: block;
        height: 100%;
        background: @bg-color;
        > header {
            display: block;
            position: relative;
            height: 48px;
            font-size: .9rem;
            color: @white;
            background-color: @primary;
            line-height: 48px;
            text-align: center;
            i {
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 64px;
                height: 48px;
                background-repeat: no-repeat;
                background-size: 32px;
                background-position: center;
                &.back {
                    background-image: url(../images/back.png);
                }
                &.home {
                    background-image: url(../images/home.png);
                }
                &.new {
                    left: auto;
                    right: 0;
                    background-image: url(../images/add-icon.png);
                }
            }
        }
        > article {
            display: block;
            position: relative;
            height: calc(~"100% - 48px");
            padding-top: 1px;
            overflow-x: hidden;
        }
    }
</style>