import Layout from './Layout'
import UserPicker from './UserPicker'
import SupplierPicker from './SupplierPicker'
import MaterialPicker from './MaterialPicker'
import ProjectPicker from './ProjectPicker'
import PurchasePicker from './PurchasePicker'
import { formatDate } from '../util'

export default Vue => {
    Vue.component('Layout', Layout)
    Vue.component('UserPicker', UserPicker)
    Vue.component('SupplierPicker', SupplierPicker)
    Vue.component('MaterialPicker', MaterialPicker)
    Vue.component('ProjectPicker', ProjectPicker)
    Vue.component('PurchasePicker', PurchasePicker)
    
    Vue.filter('dateFormat', (val, type) => {

        return formatDate(val, type)
    })

    Vue.directive('format', function (el, binding) {
        el.setAttribute('style', 'line-height: 28px; background: #f00;')
    })
}