import Ajax from './Ajax'

const purchaseAddApi = data => {
    return Ajax({
        url: '/purchase/save',
        method: 'POST',
        data
    })
}

const purchaseSaveApi = data => {
    return Ajax({
        url: '/purchase/update',
        method: 'POST',
        data
    })
}

const purchaseDeleteApi = id => {
    return Ajax({
        url: '/purchase/delete/' + id,
        method: 'POST'
    })
}

const purchaseInfoApi = id => {
    return Ajax({
        url: '/purchase/info',
        params: {id}
    })
}

const purchaseListApi = params => {
    return Ajax({
        url: '/purchase/list',
        params
    })
}

const purchaseFlowApi = data => {
    return Ajax({
        url: '/purchase/flow',
        method: 'POST',
        data
    })
}

const purchasePayApi = data => {
    return Ajax({
        url: '/purchase/payment',
        method: 'POST',
        data
    })
}

export {
    purchaseAddApi,
    purchaseSaveApi,
    purchaseDeleteApi,
    purchaseInfoApi,
    purchaseListApi,
    purchaseFlowApi,
    purchasePayApi
}