import Ajax from './Ajax'

export const supplierListApi = params => {
    return Ajax({
        url: '/supplier/list',
        params
    })
}

export const supplierAddApi = data => {
    return Ajax({
        url: '/supplier/save',
        method: 'POST',
        data
    })
}

export const supplierSaveApi = data => {
    return Ajax({
        url: '/supplier/update',
        method: 'POST',
        data
    })
}

export const supplierDeleteApi = id => {
    return Ajax({
        url: '/supplier/delete/' + id,
        method: 'POST'
    })
}