<template>
    <div class="simple-list">
        <header>
            <input type="text" v-model.lazy="name" placeholder="输入材料名字"/>
            <i @click="$emit('pickEvent')" class="close"></i>
        </header>
        <ul>
            <li v-for="it in materialList" v-show="it.show" :key="it.id">
                <span>{{it.name}}</span>
                <em>{{it.id}}</em>
                <i @click="pickMaterial(it)"></i>
            </li>
        </ul>
    </div>
</template>

<script>
    import { materialListApi } from '../apis/MaterialApi'
    export default {
        data() {
            return {
                name: '',
                materialList: []
            }
        },
        watch: {
            userName(val) {
                this.materialList = this.materialList.map(it => {
                    it.show = it.name.includes(val)
                    return it
                })
            }
        },
        async created() {
            let result = await materialListApi({page: 1, size: 100000})
            if (result.code === 200 && !!result.data) {
                this.materialList = result.data.rows.map(it => {
                    it.show = true
                    return it
                })
            }
        },
        methods: {
            pickMaterial(it) {
                this.$emit('pickEvent', it)
            }
        }
    }
</script>