import axios from 'axios'

import store from '../store'

const ajax = axios.create({
    baseURL: window.BASE_URL,
    timeout: 6000
})

ajax.interceptors.request.use(config => {
    let token = store.getters['common/getToken'],
        otherUrls = ['/user/validate/code', '/user/app/login']

    if (otherUrls.includes(config.url)) {
        return config
    } else {
        if (!token) {
            return Promise.reject({code: 400, message: '用户token失效'})
        } else {
            config.headers.token = token
            return config
        }
    }
})

function Ajax(req) {
    return new Promise(resolve => {
        ajax.request({
            url: req.url,
            method: req.method || 'GET',
            data: req.data || {},
            params: req.params || {}
        }).then(({data}) => {
            resolve(data)
        }).catch(e => {
            resolve(e)
        })
    })
}

export default Ajax