import Ajax from './Ajax'

const materialListApi = () => Ajax({url: '/material/list'})

const materialAddApi = data => {
    return Ajax({
        url: '/material/save',
        method: 'POST',
        data
    })
}

const materialSaveApi = data => {
    return Ajax({
        url: '/material/update',
        method: 'POST',
        data
    })
}

export {
    materialListApi,
    materialAddApi,
    materialSaveApi
}