<template>
    <div class="simple-list">
        <header>
            <input type="text" v-model.lazy="userName" placeholder="输入需要过滤的名字"/>
        </header>
        <ul>
            <li v-for="it in userList" v-show="it.show" :key="it.id">
                <span>{{it.name}}</span>
                <em>{{it.id}}</em>
                <i @click="pickUser(it)"></i>
            </li>
        </ul>
    </div>
</template>

<script>
    import { userListApi } from '../apis/UserApi'
    export default {
        data() {
            return {
                userName: '',
                userList: []
            }
        },
        watch: {
            userName(val) {
                this.userList = this.userList.map(it => {
                    it.show = it.name.includes(val)
                    return it
                })
            }
        },
        async created() {
            let result = await userListApi({page: 1, size: 100000})
            if (result.code === 200 && !!result.data) {
                this.userList = result.data.rows.map(it => {
                    it.show = true
                    return it
                })
            }
        },
        methods: {
            pickUser(it) {
                console.log(it);
                this.$emit('input', it)
            }
        }
    }
</script>