const common = {
    namespaced: true,
    state: {
        token: '',
        userInfo: {},
        roles: [],
        editData: {}
    },
    getters: {
        getToken: state => state.token,
        getUserInfo: state => state.userInfo,
        getRoles: state => state.roles,
        getEditData: state => state.editData
    },
    actions: {
        async setToken(store, _nt) {
            // 如果需要对数据进行处理，可以在action里边执行
            store.commit('mutationToken', _nt)
        }
    },
    mutations: {
        mutationToken(state, _nt) {
            state.token = _nt
        },
        mutationUserInfo(state, ui) {
            state.userInfo = ui
        },
        mutationRoles(state, roles) {
            state.roles = roles
        },
        mutationEditData(state, ed) {
            state.editData = ed
        }
    }
}

export default common