
export const formatDate = (val, type) => {
    let date = new Date(val),
        yyyy = date.getFullYear(),
        MM = date.getMonth() + 1,
        dd = date.getDate(),
        hh = date.getHours(),
        mm = date.getMinutes(),
        ss = date.getSeconds()

    MM = MM > 9 ? MM : `0${MM}`
    dd = dd > 9 ? dd : `0${dd}`
    hh = hh > 9 ? hh : `0${hh}`
    mm = mm > 9 ? mm : `0${mm}`
    ss = ss > 9 ? ss : `0${ss}`

    let obj = {yyyy, MM, dd, hh, mm, ss}

    return type.replace(/(yyyy)|(MM)|(dd)|(hh)|(mm)|(ss)/g, function(arg0) {
        return obj[arg0]
    })
}