import Ajax from './Ajax'

export const projectListApi = params => {
    return Ajax({
        url: '/project/list',
        params
    })
}

export const projectAddApi = data => {
    return Ajax({
        url: '/project/save',
        method: 'POST',
        data
    })
}

export const projectSaveApi = data => {
    return Ajax({
        url: '/project/update',
        method: 'POST',
        data
    })
}

export const projectDeleteApi = id => {
    return Ajax({
        url: '/project/delete/' + id,
        method: 'POST'
    })
}